										</div>
									</div>
								</div>
							</div>
							<div id="game-details-left-arrow"
								:class="`${activeIndex <= 0 || detail.length == 1?'swiper-button-hidden':''} swiper-button-prev`"
								@click="swiper.slidePrev()"
								style="top: calc(50% - 85px); background-color: rgba(0, 0, 0, 0.4);">
